@-webkit-keyframes blinknew {
  0% {
    opacity: 0;
  }

  10% {
    opacity: .1;
  }

  20% {
    opacity: .25;
  }

  30% {
    opacity: .5;
  }

  40% {
    opacity: .75;
  }

  50% {
    opacity: 1;
  }

  60% {
    opacity: .75;
  }

  70% {
    opacity: .5;
  }

  80% {
    opacity: .25;
  }

  90% {
    opacity: .1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes blinknew {
  0% {
    opacity: 0;
